/* -- Fonts -- */
/*
    * font-family: 'Montserrat', sans-serif;
    * font-family: 'Varela Round', sans-serif;
    * font-family: 'Bungee Inline', sans-serif;
    * font-family: 'Material Icons', sans-serif;
    * font-family: 'Roboto', sans-serif;
*/
/* -- Presets & Resets -- */
/* -- Elements -- */
*, *:before, *:after {
  box-sizing: border-box;
  font-size: 100%;
  outline: none;
  resize: none;
  border: none;
  padding: 0;
  margin: 0;
}

html,
body {
  text-rendering: optimizeLegibility;
  font-family: "Roboto", sans-serif;
  background: #f5f8fc;
  word-wrap: break-word;
  line-height: 1.15;
  font-weight: 400;
  font-size: 15px;
  color: #222;
}

@media (max-width: 300px) {
  html,
  body {
    word-break: break-all;
  }
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: inherit;
  line-height: 1.15;
  font-weight: 400;
  color: inherit;
  display: block;
}

h1,
.h1 {
  margin: 2.1rem 0 1.68rem 0;
  font-weight: 900;
}

h2,
.h2 {
  margin: 1.78rem 0 1.424rem 0;
  font-weight: 700;
}

h3,
.h3 {
  margin: 1.46rem 0 1.168rem 0;
  font-weight: 600;
}

h4,
.h4 {
  margin: 1.14rem 0 0.912rem 0;
}

h5,
.h5 {
  margin: 0.82rem 0 0.656rem 0;
}

h6,
.h6 {
  margin: 0.5rem 0 0.4rem 0;
}

p,
.p {
  margin: 0 2em 10px 2em;
}

h1 {
  font-size: 4.2rem;
}

.h1 {
  font-size: 4.2rem !important;
}

h2 {
  font-size: 3.56rem;
}

.h2 {
  font-size: 3.56rem !important;
}

h3 {
  font-size: 2.92rem;
}

.h3 {
  font-size: 2.92rem !important;
}

h4 {
  font-size: 2.28rem;
}

.h4 {
  font-size: 2.28rem !important;
}

h5 {
  font-size: 1.64rem;
}

.h5 {
  font-size: 1.64rem !important;
}

h6 {
  font-size: 1rem;
}

.h6 {
  font-size: 1rem !important;
}

img {
  min-height: 200px;
  display: block;
  border: none;
  width: 100%;
}

.load-img {
  background: #222;
}

.placeholder-img {
  transition: opacity .8s ease;
  opacity: 1;
}

.core-img {
  position: absolute;
  left: 0;
  top: 0;
}

.core-img-show {
  opacity: 0 !important;
  z-index: 0 !important;
}

a {
  text-decoration: none;
  display: inline-block;
  color: #ff0266;
  cursor: pointer;
}

a:hover, a:focus {
  text-decoration: underline;
  color: #e5015b;
}

ul,
ol {
  list-style-type: disc;
  margin: 20px 2em;
  display: block;
}

ol {
  list-style-type: decimal;
}

ul ul,
ol ul {
  list-style-type: circle;
}

li {
  text-align: inherit;
  display: list-item;
}

.list-none {
  list-style: none;
}

