/* -- Header -- */
.header {
  position: relative;
  display: block;
  /* -- Navigation -- */
}

.header-top-spot {
  height: 85px;
}

.navbar {
  position: relative;
  max-height: 1000px;
  height: 60px;
  z-index: 1200;
  width: 100%;
}

.navbar-focus .navbar-layout, .navbar-show .navbar-layout, .navbar:hover .navbar-layout {
  border-bottom-color: #dbdbdb;
  border-right-color: #dbdbdb;
  border-left-color: #dbdbdb;
  background: #fff;
}

.navbar-focus .navbar-title-line, .navbar-show .navbar-title-line, .navbar:hover .navbar-title-line {
  border-left-color: #bed2ec !important;
}

.navbar:hover .navbar-layout {
  border-bottom-color: #4500ff;
  border-right-color: #4500ff;
  border-left-color: #4500ff;
}

.navbar-fixed {
  position: fixed;
  left: 0;
  top: 0;
}

.navbar-layout {
  border-bottom: 1px solid transparent;
  border-right: 1px solid transparent;
  border-left: 1px solid transparent;
  border-radius: 0 0 18px 18px;
  background: transparent;
  height: 60px;
  margin: 0 auto;
}

.navbar-head {
  border-bottom: 1px solid transparent;
  flex-direction: row;
  align-items: center;
  flex-wrap: nowrap;
  display: flex;
}

.navbar-logo {
  transform: translateY(4px);
  display: inline-block;
  min-height: 22px;
  width: 22px;
}

.navbar-title {
  font-family: "Montserrat", sans-serif;
  letter-spacing: 0.05em;
  text-decoration: none;
  line-height: 60px;
  padding-left: 25px;
  position: relative;
  font-size: 17px;
}

.navbar-title-line {
  transition: border-left-color linear .45s;
  border-left: 1px solid transparent;
  border-left-color: #d8d8d8;
  margin-right: 10px;
  margin-left: 10px;
  display: inline-block;
  height: 14px;
}

.navbar-list {
  justify-content: center;
  align-items: center;
  white-space: nowrap;
  overflow-y: hidden;
  list-style: none;
  display: flex;
  width: 100%;
  margin: 0;
}

.navbar-link {
  text-transform: uppercase;
  font-family: "Montserrat", sans-serif;
  text-decoration: none;
  line-height: 60px;
  letter-spacing: 1px;
  font-weight: 300;
  padding: 0 28px;
  font-size: 14px;
  color: #000;
  width: 100%;
}

.navbar-link-content {
  display: inline-block;
}

.navbar-link-content:before {
  transition: transform 0.25s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  transform: translateY(46px) scaleX(0);
  background-color: #4500ff !important;
  border: 1.5px solid transparent;
  transform-origin: 100% 50%;
